
import Vue from "vue";
import store from "@/store";

export default Vue.extend({
  name: "CarnetIssuance",

  props: {
    value: {
      required: false,
      default: () => ({})
    },
    loadingData: {
      required: true,
      default: false,
      type: Boolean
    }
  },

  data: () => ({
    model: {} as any,
    paymentsLog: [] as Array<any>,
    guaranteeLog: [] as Array<any>,
    isLoading: false as boolean
  }),

  watch: {
    value: {
      immediate: true,
      handler() {
        // @ts-ignore>
        this.model = this.value;
      }
    },
    loadingData: {
      immediate: true,
      handler() {
        //@ts-ignore
        if (this.loadingData) {
          //@ts-ignore
          this.loadData();
        }
      }
    }
  },

  computed: {
    totalSumWithVat() {
      return this.model.details.services?.reduce(
        (total: number, { sum_with_vat }: { sum_with_vat: number }) => {
          if (sum_with_vat) {
            return total + Number(sum_with_vat);
          }
          return total;
        },
        0
      );
    },
    totalRemainedForPayServiceSUm() {
      return this.model.details.services?.reduce(
        (total: number, { remained_sum }: { remained_sum: number }) => {
          if (remained_sum) {
            return total + Number(remained_sum);
          }
          return total;
        },
        0
      );
    },
    totalSum() {
      return this.model.details.services?.reduce(
        (total: number, { sum_no_vat }: { sum_no_vat: number }) => {
          if (sum_no_vat) {
            return total + Number(sum_no_vat);
          }
          return total;
        },
        0
      );
    },
    totalPayedServiceSUm() {
      return this.model.details.services?.reduce(
        (total: number, { paid_sum }: { paid_sum: number }) => {
          if (paid_sum) {
            return total + Number(paid_sum);
          }
          return total;
        },
        0
      );
    },
    totalPayed() {
      return this.paymentsLog.reduce(
        (total: number, { sum }: { sum: number }) => {
          if (sum) {
            return total + Number(sum);
          }
          return total;
        },
        0
      );
    },
    totalGuaranteePaid() {
      return this.guaranteeLog.reduce(
        (total: number, { sum }: { sum: number }) => {
          if (sum) {
            return Math.floor((total + Number(sum)) * 100) / 100;
          }
          return Math.floor(total * 100) / 100;
        },
        0
      );
    }
  },

  methods: {
    async loadData() {
      this.isLoading = true;
      try {
        const [paymentLog, guaranteeLog] = await Promise.all([
          this.$API.payments().getOrderPayments(Number(this.$route.params.id)),
          this.$API
            .guarantees()
            .getOrderGuarantees(Number(this.$route.params.id))
        ]);
        this.paymentsLog = paymentLog;
        this.guaranteeLog = guaranteeLog;
      } catch (e) {
        await this.$store.dispatch("alert/showError", e.message || e);
      }

      this.isLoading = false;
    },
    downloadFile(file: any) {
      const element = document.createElement("a");
      element.setAttribute("href", file.url);
      element.setAttribute("target", "_blank");
      element.click();
    },
    async issueOrder() {
      try {
        await this.$API.orders().issueOrder(this.$route.params.id);
        this.$emit("update");
      } catch (e) {
        await store.dispatch("alert/showError", e.message);
      }
    }
  }
});
